import { DEFAULT_APP_CATEGORIES } from '../../../src/core/public';
import './components/context_menu/context_menu';
import { PLUGIN_NAME } from '../common';
export class OpendistroKibanaReportsPlugin {
  setup(core) {
    // Register an application into the side navigation menu
    core.application.register({
      id: PLUGIN_NAME,
      title: 'Reporting',
      category: DEFAULT_APP_CATEGORIES.kibana,
      order: 8037,

      async mount(params) {
        // Load application bundle
        const {
          renderApp
        } = await import('./application'); // Get start services as specified in kibana.json

        const [coreStart, depsStart] = await core.getStartServices(); // Render the application

        return renderApp(coreStart, depsStart, params);
      }

    }); // Return methods that should be available to other plugins

    return {};
  }

  start(core) {
    return {};
  }

  stop() {}

}